import Vue from 'vue';
import Vuex, { MutationTree } from 'vuex';
import { SWITCH_TABBAR, HIDE_TABBAR, SHOW_TABBAR, SHOW_SNACKBAR, CLOSE_SNACKBAR } from '@/store/types';
import { RootState, RootShowSnackBarPayload } from '@/store/interface';
import Home from './Home/index';

Vue.use(Vuex);

const state: RootState = {
  showTabbar: true,
  snackBar: {
    value: false,
    message: '',
    timeout: 6000,
    color: 'error',
  },
}
const mutations: MutationTree<RootState> = {
  [SWITCH_TABBAR](state: RootState) {
    state.showTabbar = !state.showTabbar;
  },
  [HIDE_TABBAR](state: RootState) {
    state.showTabbar = false;
  },
  [SHOW_TABBAR](state: RootState) {
    state.showTabbar = true;
  },
  [SHOW_SNACKBAR](state: RootState, { timeout = 6000, message, color = 'error' }: RootShowSnackBarPayload) {
    state.snackBar.value = true;
    state.snackBar.message = message;
    state.snackBar.timeout = timeout;
    state.snackBar.color = color;
  },
  [CLOSE_SNACKBAR](state: RootState) {
    state.snackBar.value = false;
    state.snackBar.color = 'error';
    state.snackBar.timeout = 6000;
    state.snackBar.message = '';
  }
}
export default new Vuex.Store({
  state,
  mutations,
  modules: {
    Home,
  },
});
