import Vue from 'vue';
import Router from 'vue-router';
import store from '@/store';
import { HIDE_TABBAR } from '@/store/types';

const Root = () => import('@/views/Root/index.vue');
const Home = () => import('@/views/Home/index.vue');
const Message = () => import('@/views/Message/index.vue');
const Me = () => import('@/views/Me/index.vue');
const Favorite = () => import('@/views/Favorite/index.vue');
const TopicDetail = () => import('@/views/TopicDetail/index.vue');
const NotFoundPage = () => import('@/views/404.vue');

Vue.use(Router);

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      component: Root,
      redirect: '/home',
      children: [
        {
          path: '/home',
          component: Home,
        },
        {
          path: '/message',
          component: Message,
        },
        {
          path: '/favorite',
          component: Favorite,
        },
        {
          path: '/me',
          component: Me,
        },
      ],
    },
    {
      path: '/topic/:id',
      component: TopicDetail,
    },
    {
      path: '/topic/:id/reply',
      component: TopicDetail,
      meta: {
        reply: true,
      },
    },
    {
      path: '*',
      component: NotFoundPage,
    },
  ],
});
