







import { Component, Vue } from 'vue-property-decorator';
import { State, Mutation } from 'vuex-class';
import { SnackBar, RootState } from '@/store/interface';
import { CLOSE_SNACKBAR } from '@/store/types';

@Component
export default class CSnackBar extends Vue {
  @State((state: RootState) => state.snackBar)
  private snackBar!: SnackBar;
  @Mutation(CLOSE_SNACKBAR)
  private close!: Function;
  private input (v: boolean){
    if (v === false) {
      this.close();
    }
  }
}
