import { Module, ActionContext } from 'vuex';
import { RootState, HomeState, Page, RootShowSnackBarPayload } from '@/store/interface';
import { Tabs, TABS } from '@/enum';
import { Home, SHOW_SNACKBAR } from '@/store/types';

const length = TABS.length;

const home: Module<HomeState, RootState> = {
  namespaced: true,
  state: {
    tab: Tabs.all,
    pages: {
      ask: 1,
      share: 1,
      all: 1,
      good: 1,
      job: 1,
      dev: 1,
    }
  },
  mutations: {
    [Home.NEXT_PAGE](state: HomeState, payload: Tabs) {
      const tab: Page = <Page>Tabs[payload];
      state.pages[tab] += 1;
      return true;
    },
    [Home.NEXT_TAB](state: HomeState) {
      let tab = state.tab;
      tab += 1;
      if (tab >= length) {
        tab %= length;
      }
      state.tab = tab;
    },
    [Home.PREV_TAB](state: HomeState) {
      let tab = state.tab;
      tab -= 1;
      if (tab < 0) {
        tab += length;
      }
      state.tab = tab;
    },
    [Home.SET_TAB](state: HomeState, payload: Tabs) {
      state.tab = payload;
    },
  },
  getters: {
    [Home.getter.currentPage](state: HomeState) {
      return (tab: Tabs) => {
        const tabName: Page = <Page>Tabs[tab];
        return state.pages[tabName] as number;
      }
    }
  },
  actions: {
    [Home.PREV_PAGE]({state, commit}: ActionContext<HomeState, RootState>, payload: Tabs) {
      const tab: Page = <Page>Tabs[payload];
      if (state.pages[tab] > 1) {
        state.pages[tab] -= 1;
        return true;
      } else {
        commit(SHOW_SNACKBAR, { message: '已经是第一页了！', color: 'info' } as RootShowSnackBarPayload, { root: true });
        return false;
      }
    },
  }
}

export default home;
